import axios from 'axios';
import React, { useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Paper from '@material-ui/core/Paper';
import Spinner from './Spinner';
import Error from './Error';

const useStyles = makeStyles({
  table: {
    minWidth: 650,
  },
});


function Holidays({Country}) {
    const [result, setResult] = React.useState([]);
    const [loading, setLoading] = React.useState(true);
    const [error, setError] = React.useState(false);
    const [errormsg, setErrorMsg] = React.useState([]);

    const thisyear = new Date().getFullYear();
    const APIcountry =`https://date.nager.at/api/v2/publicholidays/${thisyear}/${Country}`;
    useEffect(() => {
        const consultarApi = async () => {
            await axios.get(APIcountry, {
                method: 'GET', // *GET, POST, PUT, DELETE, etc.
                // mode: 'cors', // no-cors, *cors, same-origin
                // cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
                credentials: 'same-origin', // include, *same-origin, omit
                headers: {
                  'Content-Type': 'application/json',
                  'Access-Control-Allow-Origin' : 'https://Jotak1.github.io/'

                //   'Content-Type': 'application/x-www-form-urlencoded',
                },
                redirect: 'follow', // manual, *follow, error
                referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
                // body: JSON.stringify(response) // body data type must match "Content-Type" header
              })
            .then(response => {
                setResult(response.data);
                setLoading(false);
            })
            .catch(err => {
                
                setErrorMsg(err);
                setError(true);
                 setLoading(false);
            })
        
           
        };
        consultarApi();
    },[]);

    const classes = useStyles();

    return (
        <div>
            {loading ? <Spinner/> : (error ?  <Error errormsg={errormsg}/> : (
           <TableContainer component={Paper}>
            <Table className={classes.table} size="small" aria-label="a dense table">
                <TableHead>
                <TableRow>
                    <TableCell>Date</TableCell>
                    <TableCell align="right">Local Name</TableCell>
                    <TableCell align="right">Name</TableCell>
                </TableRow>
                </TableHead>
                <TableBody>
                {result.map((row) => (
                    <TableRow key={row.date+row.localName}>
                    <TableCell component="th" scope="row">
                        {row.date}
                    </TableCell>
                    <TableCell align="right">{row.localName}</TableCell>
                    <TableCell align="right">{row.name}</TableCell>
                    </TableRow>
                ))}
                </TableBody>
            </Table>
            </TableContainer>) )}
        </div>
    );
}
export default Holidays;